<style lang="scss">
  @import 'header.scss';
</style>
<template>
  <div>
    <Header>
      <Menu mode="horizontal" theme="dark" active-name="1">
        <!--<div class="layout-logo"></div>-->
        <div class="layout-nav" :style="{float:'right'}">
          <MenuItem name="5">
            <div @click="showPasswordChange=true">
              <Icon type="ios-key" title="" :style="{marginRight:'6px'}"></Icon>
              <span></span>
            </div>
          </MenuItem>
          <MenuItem name="6">
            <div @click="showLogout=true">
              <Icon type="ios-log-out" title="" :style="{marginRight:'6px'}"></Icon>
              <span>
              </span></div>
          </MenuItem>

        </div>
      </Menu>
    </Header>
    <Modal
      v-model="showLogout"
      title=""
      width="20%">
      ?
      <div slot="footer">
        <Button type="default" @click="showLogout=false"></Button>
        <Button type="info" @click="showLogout=false;logout()"></Button>
      </div>
    </Modal>
    <Modal
      v-model="showPasswordChange"
      title=""
      width="40%">
      <Row>
        <Form :label-position="'right'" :label-width="100" :model="passwordChange" :rules="rulePasswordChange"
              ref="passwordChange">
          <Row>
            <Col span="16">
              <FormItem label="" prop="oldPassword">
                <Input type="password" placeholder="" v-model="passwordChange.oldPassword"></Input>
              </FormItem>
            </Col>
          </Row>
          <Row>
            <Col span="16">
              <FormItem label="" prop="newPassword">
                <Input type="password" placeholder="" v-model="passwordChange.newPassword"></Input>
              </FormItem>
            </Col>
          </Row>
          <Row>
            <Col span="16">
              <FormItem label="" prop="newPassword">
                <Input type="password" placeholder="" v-model="passwordChange.newPasswordCheck"></Input>
              </FormItem>
            </Col>
          </Row>
        </Form>
      </Row>
      <div slot="footer">
        <Button type="default" @click="showPasswordChange=false"></Button>
        <Button type="info" @click="changePassword('passwordChange')"></Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import {logoutService} from '../../page/login/service'

  export default {
    data () {
      return {
        showUserInfo: false,
        tenantList: JSON.parse(localStorage.getItem('user_info')),
        showMessage: false,
        showLogout: false,
        showPasswordChange: false,
        passwordChange: {
          oldPassword: '',
          newPassword: '',
          newPasswordCheck: ''
        },
        rulePasswordChange: {
          oldPassword: [
            {required: true, message: '', trigger: 'blur'},
            // {type: 'string',pattern:/^[0-9A-Za-z]{6,}$/, message: '',trigger: 'blur'},

          ],
          newPassword: [
            {required: true, message: '', trigger: 'blur'},
            // {type: 'string',pattern:/^[0-9A-Za-z]{6,}$/, message: '6',trigger: 'blur'},
            {
              type: 'string',
              min: 6,
              max: 16,
              message: '6-16',
              trigger: 'blur'
            }
          ],
          newPasswordCheck: [
            {required: true, message: '', trigger: 'blur'},
            {
              type: 'string',
              min: 6,
              max: 16,
              message: '6-16',
              trigger: 'blur'
            }
            // {type: 'string',pattern:/^[0-9A-Za-z]{6,}$/, message: '6',trigger: 'blur'},
            // {type: 'string',pattern:this.passwordChange.newPassword=this.passwordChange.newPasswordCheck, message: '',trigger: 'blur'},
          ]
        }
      }
    },
    created () {
      // 
      // this.getTenantMessage()
    },
    methods: {
      showMessageFun () {
        this.showMessage = !this.showMessage
      },

      async logout () {
        let res = await logoutService()
        if (res) {
          this.$router.push('/')
        }
      },
      changePassword (name) {
        this.$refs[name].validate(valid => {
          if (valid) {
            if (this.passwordChange.newPassword === this.passwordChange.newPasswordCheck) {
              const self = this
              let data = self.passwordChange
              // console.log(data);
              self.globalMethod.showLoading('')
              self.axios.post('/api/user/updatePassword', data).then(function (response) {
                self.globalMethod.dealResponse(response, function () {
                  self.globalMethod.showSuccess('!')
                  self.showPasswordChange = false
                  self.logout()
                })
              })
            } else {
              this.$Message.error('')
            }
          } else {
            this.$Message.error('')
          }
        })
      }
    }
  }
</script>



